.App {
  align-items: center;
  background: #EDC4A7;;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  text-align: center;
  
}

.Box{
  background: #FFFFFF;
  border-radius: 10px; 
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  height: 895px; 
  width: 607px;
}

button{
  background: linear-gradient(269.05deg, #273A96 0%, #35C4F4 100%);
  border-radius: 41px;
  color: white;
  font-weight: 700;
  font-size: 18px;
  height: 62px;
  width: 481px;
}

.inputBox{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.inputBox input{
  border: none;
  border-bottom: 2px solid grey;
  margin: 20px 70px;
  padding: 10px;
}

.Message{
  margin-top: 40px;
}